* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

:root {
  --orange: #fd7e14;
  --yellow: #ffc107;
  --white: #fff;
  --primary: #da9f5b;
  --secondary: #33211d;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #fffbf2;
  --dark: #111;
}

body {
  width: 100%;
}

.navbar {
  background-color: var(--secondary);
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: #5e0c0c 1px solid;
}
/* @media (max-width: 500px)  */
@media (max-width: 730px) {
  .navbar {
    flex-direction: column;
    align-items: center;
  }
  .navbar-item {
    padding: 0.7rem 0 0 0;
  }
}

.navbar .navbar-brand {
  font-size: 2.7rem;
  font-weight: 900;
  color: var(--light);

  animation-name: brand;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
@keyframes brand {
  0% {
    color: var(--primary);
  }
  50% {
    color: var(--light);
  }
  100% {
    color: var(--primary);
  }
}

.navbar .navbar-item {
  font-size: 1.2rem;
  font-weight: 600;
}

.navbar .navbar-item a {
  padding: 10px;
  text-decoration: none;
  color: var(--light);
}

.navbar .navbar-item a:hover {
  color: var(--primary);
}

.b {
  background-color: var(--secondary);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  width: 100%;
}

/* @media (max-width: 500px)  */
@media (max-width: 730px) {
  .b .body_container {
    flex-direction: column;
    align-items: center;
  }
  .b .body_container .body_left {
    text-align: center;
  }
  .b .body_container .body_left p {
    font-size: 14px !important;
  }
  .b .body_container .body_left h2 {
    font-size: 60px !important;
  }
  .b .body_container .body_left h4 {
    font-size: 20px !important;
  }
}

.b .body_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 600px;
  width: 100%;
}

.b .body_container .body_left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 90%;
  width: 50%;
}

.b .body_container .body_left p {
  font-size: 30px;
  font-weight: 400;
  color: var(--primary);
}

.b .body_container .body_left h2 {
  font-size: 90px;
  color: var(--light);
}

.b .body_container .body_left h4 {
  font-size: 40px;
  color: var(--light);
}

.b .body_container .body_right {
  background-image: url(./img/5coffee.png);
  background-repeat: no-repeat;
  background-size: contain;
  height: 90%;
  width: 50%;
}

.body_Menu {
  /* height: 700px; */
  width: 100%;
  background-color: antiquewhite;
}

.body_Menu .overlay-top,
.overlay-b {
  padding: 1.5rem;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100px;
}

.overlay-top {
  background-image: url(./img/overlay-top.png);
}

.Menu_Title {
  font-size: 40px;
  font-weight: 600;
  text-align: center;
}

.Menu_img {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.Menu_img img {
  width: 600px;
  height: 500px;
  padding: 1rem;
  opacity: 0.7;
  overflow: hidden;
}

.Menu_img img:hover {
  opacity: 1;
  box-shadow: 10px 10px 20px 20px var(--secondary);
}

.overlay-b {
  background-image: url(./img/overlay-bottom.png);
}

.offers {
  background-image: url(./img/banner-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 2.4rem;
}

.offers .offers__container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  height: 640px;
  padding: 2rem;
  flex-wrap: wrap;
}

.offers .offers__container .offers__title {
  font-size: 50px;
  font-weight: 600;
  color: var(--primary);
  background-color: var(--secondary);
  border-radius: 20px;
  padding: 10px;
}

.offers img {
  height: 550px;
  border-radius: 20px;
}
@media (max-width: 700px) {
  .offers img {
    max-height: 400px;
  }
}

.about .footer a {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: var(--primary);
  text-decoration: none;
  font-size: 3rem;
  font-weight: 600;
}

.about {
  background-image: url(./img/3coffee.jpg);
  background-size: cover;
  background-position: 50%;
  height: 880px;
  overflow: hidden;
}
@media (max-width: 700px) {
  .about {
    height: 300px !important;
  }
}
